@import 'assets/styles/_dimensions.scss';

nav {
  border-bottom: 1px solid rgb(77, 77, 77);
  background-color: #1c1817;
  .button-group {
    position: absolute;
    left: 50%;
    transform: translateX(-100%);

    @media (max-width: 990px) {
      position: relative;
      left: 0;
      transform: none;
      width: fit-content;
      margin: auto;
    }

    .isConnected {
      @media (max-width: 450px) {
        flex-direction: column;
      }
    }
  }
  .navbar-nav {
    .nav-link {
      font-family: 'Wendy one';
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: 30px;
      &:hover {
        color: #9c3d47;
      }
      &.active, &.show {
        color: #ffb8d3;
      }
    }
  }
}
