.hero {
  position: relative;
  margin-bottom: 5rem;
  padding-top: 10rem;

  @media (max-width: 450px) {
    padding-top: 6rem;
  }

  h1 {
    text-transform: uppercase;
    font-size: 6rem;
    text-align: center;
    font-family: "Wendy one";
    margin-bottom: 3rem;

    span {
      color: #f9abc3;
    }

    @media (max-width: 991px) {
      font-size: 4rem;
    }

    @media (max-width: 768px) {
      font-size: 3rem;
    }

    @media (max-width: 450px) {
      font-size: 2rem;
    }
  }

  p.note {
    text-align: center;
    font-size: 2rem;
    width: 80%;
    margin: auto;
    line-height: 2.2rem;
    margin-bottom: 5rem;
    font-family: Kalam;

    span {
      color: #f9abc3;
    }

    @media (max-width: 991px) {
      font-size: 1.6rem;
    }

    @media (max-width: 768px) {
      font-size: 1.2rem;
    }

    @media (max-width: 768px) {
      margin-bottom: 3rem;
    }
  }

  img {
    position: absolute;
    z-index: -1;
    width: 200px;

    @media (max-width: 1200px) {
      width: 150px;
    }

    @media (max-width: 768px) {
      width: 120px;
    }

    @media (max-width: 450px) {
      width: 80px;
    }

    &.coin1 {
      top: 0;
      left: 0;
    }

    &.coin2 {
      bottom: 0;
      right: 0;
    }
  }

  .betting-closed-container {
    position: relative;
    display: flex;
    padding-top: 4rem;
    justify-content: center;
    font-family: 'Kalam';
    text-align: center;

    p.betting-closed {
      font-size: 2rem;

      @media (max-width: 768px) {
        font-size: 1.6rem;
      }
    }

    p.time {
      font-size: 3rem;
      color: #F9ABC3;

      @media (max-width: 768px) {
        font-size: 2.5rem;
      }
    }

    img#bow-girl {
      position: absolute;
      right: 0;
      top: -5rem;
      width: 400px;
      z-index: -1;

      @media (max-width: 768px) {
        width: 300px;
      }

      @media (max-width: 450px) {
        width: 300px;
      }
    }
  }
}

.explanation {
  background-color: #eaa1b7;
  position: relative;
  width: 100%;
  transform: skewY(-3deg);
  margin-bottom: 8rem;
  font-family: Kalam;
  font-style: normal;
  font-weight: bold;

  &>div {
    transform: skewY(3deg);

    img#bigteacher {
      width: 740px;
      transform: scaleX(-1);

      @media (max-width: 1600px) {
        width: 600px;
      }

      @media (max-width: 1440px) {
        width: 500px;
      }

      @media (max-width: 991px) {
        width: 300px;
      }

      @media (max-width: 768px) {
        width: 200px;
      }
    }

    .content {
      text-align: center;
      font-size: 2rem;

      @media (max-width: 1440px) {
        font-size: 1.6rem;
      }

      @media (max-width: 991px) {
        font-size: 1.3rem;
      }

      @media (max-width: 768px) {
        font-size: 1.2rem;
      }

      p.nft-betting {
        text-align: center;
        font-size: 3rem;
        font-family: "Wendy one";
        font-weight: 400;
        line-height: 3rem;

        @media (max-width: 1440px) {
          font-size: 2rem;
          width: 40%;
        }

        @media (max-width: 1200px) {
          font-size: 1.8rem;
          width: 40%;
        }

        @media (max-width: 991px) {
          font-size: 1.4rem;
          line-height: 1.4rem;
        }

        @media (max-width: 768px) {
          font-size: 1rem;
        }

        span {
          color: #983741;
        }
      }

      img#arrow {
        width: 160px;

        @media (max-width: 1440px) {
          width: 120px;
        }

        @media (max-width: 991px) {
          width: 80px;
        }

        @media (max-width: 768px) {
          width: 50px;
        }
      }

      img#coinstack {
        width: 200px;

        @media (max-width: 1440px) {
          width: 120px;
        }

        @media (max-width: 991px) {
          width: 80px;
        }

        @media (max-width: 768px) {
          width: 50px;
        }
      }
    }
  }

  padding: 3% 2%;

  img#coin3 {
    position: absolute;
    bottom: 0;
    bottom: -10%;
    width: 160px;
    left: 50%;

    @media (max-width: 1200px) {
      width: 120px;
    }

    @media (max-width: 991px) {
      width: 100px;
    }

    @media (max-width: 768px) {
      width: 80px;
    }

    @media (max-width: 450px) {
      width: 60px;
      bottom: -5%;
    }
  }

  img#heart {
    position: absolute;
    bottom: -15%;
    width: 250px;
    rotate: 30deg;

    @media (max-width: 1200px) {
      width: 200px;
    }

    @media (max-width: 991px) {
      width: 160px;
    }

    @media (max-width: 768px) {
      width: 120px;
      bottom: -10%;
    }

    @media (max-width: 768px) {
      width: 100px;
      bottom: -5%;
    }
  }
}

.betting-table-cointainer {
  position: relative;

  table.betting-table {
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    font-size: 2rem;
    border-collapse: collapse;
    font-family: "Kalam";
    margin-bottom: 8rem;

    th {
      background-color: #87a1d6;
    }

    th,
    td {
      border-bottom: 1px dashed #ddd;
      padding: 8px;
    }

    tr:last-child {

      th,
      td {
        border: none;
      }
    }

    @media (max-width: 991px) {
      font-size: 1.6rem;
    }

    @media (max-width: 768px) {
      font-size: 1.3rem;
    }

    @media (max-width: 576px) {
      padding-left: 1rem;
      font-size: 1rem;
    }
  }

  img#coin4 {
    position: absolute;
    width: 120px;
    bottom: -20%;
    rotate: 90deg;
    left: -3%;

    @media (max-width: 768px) {
      bottom: -30%;
    }

    @media (max-width: 450px) {
      width: 80px;
    }
  }

  img#coin5 {
    position: absolute;
    width: 120px;
    bottom: -35%;
    left: 3%;

    @media (max-width: 768px) {
      bottom: -45%;
      left: 10%;
    }

    @media (max-width: 450px) {
      width: 80px;
    }
  }
}

.nft-betting {
  h2 {
    text-transform: uppercase;
    font-size: 3rem;
    text-align: center;
    font-family: "Wendy one";
    margin-bottom: 3rem;

    span {
      color: #f9abc3;
    }

    @media (max-width: 991px) {
      font-size: 2.5rem;
    }

    @media (max-width: 768px) {
      font-size: 2.2rem;
    }

    @media (max-width: 450px) {
      font-size: 1.6rem;
    }
  }

  p.note {
    text-align: center;
    font-size: 2rem;
    width: 80%;
    margin: auto;
    line-height: 2.2rem;
    margin-bottom: 5rem;
    font-family: Kalam;

    span {
      color: #f9abc3;
    }

    @media (max-width: 991px) {
      font-size: 1.6rem;
    }

    @media (max-width: 768px) {
      font-size: 1.2rem;
    }

    @media (max-width: 768px) {
      margin-bottom: 3rem;
    }
  }
}

table.earned-tickets-table {
  border-collapse: collapse;
  border: 1px solid #3e3b3a;
  width: 100%;
  text-align: center;
  font-size: 1.4rem;
  font-style: italic;
  font-family: 'kalam';

  tbody {
    tr {
      th {
        img {
          width: 70px;
        }

        @media (max-width: 1200px) {
          img {
            width: 50px;
          }

          font-size: 1.2rem;
        }
      }

      td {
        padding: 0rem 0.2rem;
        border-bottom: 1px solid #3e3b3a;
        font-family: 'mont';
      }

      &:first-child {
        td.image-cell {
          width: 60%;

          @media (max-width: 1200px) {
            width: 80%;
          }

          img {
            width: 16.66%;
          }
        }
      }

      &:nth-child(2) {
        td.image-cell {
          img {
            width: 16.66%;
          }
        }
      }

      &:nth-child(3) {
        td.image-cell {
          img {
            width: 16.66%;
          }
        }
      }

      &:last-child {
        td.image-cell {
          img {
            width: 16.66%;
          }
        }
      }

      td.image-cell {
        img {
          width: 14%;
        }

        min-height: 200px;
        max-height: 300px;
      }
    }
  }

  th {
    color: #5a7d8d;
    border-bottom: 1px solid #3e3b3a;
    text-transform: uppercase;
    padding: 0.5rem;
  }

  .nft-image {
    cursor: pointer;

    &:hover {
      transform: scale(1.1);
    }
  }
}

.betting-open-container {
  .overview {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    font-family: 'Wendy One';
    font-size: 2rem;
    margin-bottom: 3rem;

    @media (max-width: 991px) {
      font-size: 1.6rem;
    }

    @media (max-width: 768px) {
      font-size: 1.2rem;
      margin-bottom: 2.5rem;
    }

    @media (max-width: 450px) {
      font-size: 1rem;
      margin-bottom: 2rem;
    }

    table {
      margin-bottom: 3rem;

      @media (max-width: 991px) {
        margin-bottom: 2.5rem;
      }

      @media (max-width: 768px) {
        margin-bottom: 2rem;
      }

      td {
        padding-right: 1rem;
        padding-left: 1rem;
      }
    }

    button {
      width: 14rem;

      @media (max-width: 991px) {
        width: 12rem;
      }

      @media (max-width: 768px) {
        width: 10rem;
      }
    }
  }

  .combo {
    max-width: 800px;
    margin: 0 auto 0.5rem auto;

    @media (max-width: 991px) {
      max-width: 600px;
    }

    * {
      font-family: 'Wendy one';
    }

    [class*="singleValue"] {
      font-size: 1.5rem;

      @media (max-width: 991px) {
        font-size: 1.3rem;
      }

      @media (max-width: 768px) {
        font-size: 1.2rem;
      }

      @media (max-width: 450px) {
        font-size: 1.2rem;
      }
    }
  }

  text-align: center;

  .gotcha-betting-tabs {
    font-family: 'Kalam';

    ul.react-tabs__tab-list {
      font-size: 2rem;
      font-family: 'mont';
      color: white;
      text-transform: uppercase;
      max-width: 1400px;
      margin: 2rem auto;

      @media (max-width: 991px) {
        font-size: 1.6rem;
      }

      @media (max-width: 768px) {
        font-size: 1.4rem;
      }

      @media (max-width: 450px) {
        font-size: 1.1rem;
      }

      li.react-tabs__tab {
        &.react-tabs__tab--selected {
          background-color: #1c1817;
          color: white;

          &:focus-visible {
            outline: none;
          }

          &:focus {
            &::after {
              display: none;
            }
          }
        }

        span {
          color: #9f2c3c;
        }
      }
    }

    .react-tabs__tab-panel {
      .no-nft {
        text-align: center;
        font-size: 2rem;
        color: #9f2c3c;
        padding: 2rem;

        img {
          max-width: 200px;
          margin-bottom: 1rem;
        }
      }
    }

    .cards-wrapper {
      max-height: 100vh;
      max-width: 1400px;
      margin: 0 auto;
      overflow: auto;
      display: grid;
      gap: 1rem;
      grid-template-columns: repeat(5, 1fr);

      @media (max-width: 1200px) {
        grid-template-columns: repeat(4, 1fr);
      }

      @media (max-width: 991px) {
        grid-template-columns: repeat(3, 1fr);
      }

      @media (max-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
      }

      @media (max-width: 450px) {
        grid-template-columns: repeat(1, 1fr);
      }
    }

    .staked-card {
      position: relative;
      display: flex;
      gap: 0.5rem;
      padding: 0.5rem;
      align-items: center;
      margin-bottom: 1rem;
      border: 1px solid #a5a4a3;
      border-radius: 1rem;
      text-align: left;

      .img-container {
        width: 100%;
      }

      .gotcha-checkbox {
        input+label {
          width: 21px;
          height: 20px;
        }

        position: absolute;
        bottom: 0.5rem;
        right: 1rem;
      }

      .auto-restake-btn {
        background-color: transparent;
        border: none;
        outline: none;
        width: 20px;
        padding: 0;
      }
    }

    .btn-group {
      display: flex;
      gap: 1rem;
      margin-left: 70px;

      @media screen and (max-width: 640px) {
        margin-left: 0;
        flex-direction: column;
      }

      .select-all-btn {
        background-color: transparent;
        text-decoration: underline;
        outline: none;
        border: none;
        color: #5d9aae;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-style: italic;
        font-family: 'mont';
      }
    }

    .gotcha-checkbox {
      input[type='checkbox'] {
        display: none;
      }

      input+label {
        display: inline-block;
        width: 52px;
        height: 50px;
        cursor: pointer;
        background-image: url(../assets/images/checkbox-unchecked.png);
        background-size: cover;
      }

      input[type='checkbox']:checked+label {
        background-image: url(../assets/images/checkbox-checked.png);
      }
    }
  }

  .bettings {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    font-family: 'Wendy One';
    font-size: 2rem;
    gap: 1rem;
    margin-bottom: 3rem;

    @media (max-width: 991px) {
      font-size: 1.6rem;
    }

    @media (max-width: 768px) {
      font-size: 1.2rem;
      margin-bottom: 2.5rem;
    }

    @media (max-width: 450px) {
      font-size: 1rem;
      margin-bottom: 2rem;
    }


    .prev-card {
      border: 1px solid #ffffff;
      border-radius: 2rem;
      padding: 1rem;

      .title {
        text-align: left;
        color: #ff0000;
      }

      table {
        margin-bottom: 3rem;

        @media (max-width: 991px) {
          margin-bottom: 2.5rem;
        }

        @media (max-width: 768px) {
          margin-bottom: 2rem;
        }

        td {
          padding-right: 1rem;
          padding-left: 1rem;
        }
      }
    }

    .current-card {
      border: 1px solid #ffffff;
      border-radius: 2rem;
      padding: 1rem;

      .title {
        text-align: left;
        color: #00ff00;
      }

      .bet-btn {
        min-width: 15rem;
      }

      table {
        margin-bottom: 3rem;

        @media (max-width: 991px) {
          margin-bottom: 2.5rem;
        }

        @media (max-width: 768px) {
          margin-bottom: 2rem;
        }

        td {
          padding-right: 1rem;
          padding-left: 1rem;
        }
      }
    }
  }
}